
import HeaderCop from '../common/header.vue';
import BScroll from 'vue-betterscroll';
import Scroller from 'vux/src/components/scroller/index.vue'
import XImg from 'vux/src/components/x-img/index.vue'
import Masker from 'vux/src/components/masker/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import { mapGetters } from 'vuex';
import { API, getQuery } from '../../../services';

export default {
components: {
XImg,
Actionsheet,
Group,
Cell,
Toast,
BScroll,
HeaderCop,
XTextarea
},
data() {
return {
title: "",
haveData: false,//
confrim: "",
bookList: {
status:0,
books:[]
},//
typeList:[
"","","","","","","","","",""
],//
titleArray: [
{},{},
{
title: "",//
message: ""
},
{
title: "",//
message: ""
},
{
title: "",//
message: ""
},
{
title: "",//
message: ""
},
{},
{
title: "",//
message: ""
},
{
title: "",//
message: ""
},
{
title: "",//
message: ""
}
],
toast: false,
scrollTop: 0,
page: 1,
orderId: null,
}
},
computed: {
...mapGetters([
'getUserInfoUserId',
'getUserInfoToken',
]),
},
mounted() {

},
activated() {
/* orderId */
this.orderId=this.$route.params.id;
this.getList();
},
methods: {
maxHeight(item){
item.isHeight=!item.isHeight;
let data=this.bookList;
this.bookList=[];
this.bookList=data;
setTimeout(()=>{
this.scollRefresh();
},20);
},
/**
* 
*/
getInfo(id) {
this.$router.push({
path: '/book_bookOrderMessage/' + id
});
},
/**@argument
* 
*/
listRefresh() {
this.page = 1;
this.bookList = [];
this.getList();
},
/**@argument
* 
*/
getList() {
API.book.orderDetail({
user_id: this.getUserInfoUserId,
token: this.getUserInfoToken,
order_id:this.orderId
}).then((res) => {
if(res.body.code==200){
this.bookList=res.body.data;

}
})
},
/*  */
scollRefresh() {
this.$refs.scollView.scroll.refresh();
},
/*  */
deletOrder(id) {
let self = this;
this.$vux.confirm.show({
/* title: 'Title', */
content: '',
onConfirm() {
/*  */
API.book.orderComment({
user_id: self.getUserInfoUserId,
token: self.getUserInfoToken,
order_id: id,
}).then((res) => {
if (res.body.code == 200) {
self.loading = true;
self.confrim = "";
self.toast = true;
/**@argument
* 
*/
self.getList();
}
});
}
});

},
/** */
cancelOrder(id){
let self = this;
this.$vux.confirm.show({
/* title: 'Title', */
content: '?',
onConfirm() {
/*  */
API.book.orderCancel({
user_id: self.getUserInfoUserId,
token: self.getUserInfoToken,
order_id: id,
}).then((res) => {
if (res.body.code == 200) {
self.loading = true;
self.confrim = "";
self.toast = true;
/**@argument
* 
*/
self.getList();
}
});
}
});
},
/*  */
confrimOrder(id) {
let self = this;
this.$vux.confirm.show({
/* title: 'Title', */
content: '',
onConfirm() {
/*  */
API.book.orderreceipt({
user_id: self.getUserInfoUserId,
token: self.getUserInfoToken,
order_id: id,
}).then((res) => {
if (res.body.code == 200) {
self.confrim = "";
self.toast = true;
/**@argument
* 
*/
self.getList();
}
});
}
});
},
/*  */
confirmReturn(item) {
/* * sinceId 
* expressId 
* logisticsName 
* revertId 
* company  */
let self = this;
this.$router.push({ path: '/orderReturn?orderId=' + item.order_id + '&from=book&book_id=' + item.books[0].book_id });
},
/*  , 1-2-*/
remind(id, type) {
let self = this;
API.book.subOrderMsg({
user_id: self.getUserInfoUserId,
token: self.getUserInfoToken,
order_id: id,
type: type,
}).then((res) => {
if (res.body.code == 200) {
if (type == 1) {
self.confrim = "";
} else {
self.confrim = "";
}
self.toast = true;
}
});
},
/* ,, */
download(id) {
this.$router.push({
path: '/download'
});
}
}
}






