import login from '../components/common/login.vue'; //
import logincode from '../components/common/logincode.vue'; //
import register from '../components/common/register.vue'; //
import forget from '../components/common/forget.vue'; //
import agreement from '../components/common/agreement.vue'; //
import index from '../components/index/index.vue'; //
import main from '../components/index/main.vue'; //
import car from '../components/index/car.vue'; //
import order from '../components/index/order.vue'; //
import mine from '../components/index/mine.vue'; //
import moreHot from '../components/index/moreHot.vue'; //
import chaneel from '../components/index/chaneel.vue'; //
import list from '../components/list/list.vue'; //
import search from '../components/search/search.vue'; //
import goodsList from '../components/list/goodsList.vue'; //
import goodsinfo from '../components/goods/goodsinfo.vue'; //
import goodslease from '../components/goods/goodslease.vue'; //
import authentication from '../components/mine/authentication.vue'; //
import collection from '../components/mine/collection.vue'; //
import setting from '../components/mine/setting.vue'; //
import about from '../components/mine/about.vue'; //
import help from '../components/mine/help.vue'; //
import card from '../components/mine/card.vue'; //
import opinion from '../components/mine/opinion.vue'; //
import logistics from '../components/help/logistics.vue'; //
import service from '../components/help/service.vue'; //
import transaction from '../components/help/transaction.vue'; //
import addressList from '../components/address/addressList.vue'; //
import editAddress from '../components/address/editAddress.vue'; //
import orderInfo from '../components/order/orderInfo.vue'; //
import orderReturn from '../components/order/orderReturn.vue'; //
import settlement from '../components/order/settlement.vue'; //
import orderSuccess from '../components/order/orderSuccess.vue'; //
import orderAction from '../components/order/orderAction.vue'; //
import orderLogistics from '../components/order/orderLogistics.vue'; //
import shop from '../components/shop/shop.vue'; //
import shopInfo from '../components/shop/shopInfo.vue'; //
import selected from '../components/shop/selected.vue'; //
import download from '../components/common/download.vue'; //App
import authInfo from '../components/antScore/authInfo.vue'; //
import authPage from '../components/antScore/authPage.vue'; //
import share from '../components/common/share.vue'; //
/*  */
import B_index from '../components/book/index.vue'; //
import B_home from '../components/book/home.vue'; //
import B_mine from '../components/book/mine.vue'; //
import B_car from '../components/book/car.vue'; //
import B_collect from '../components/book/collect.vue'; //
/*  */
import B_mine_plan from '../components/book/mine/plan.vue'; //
import B_mine_order from '../components/book/mine/order.vue'; //
import B_mine_setting from '../components/book/mine/setting.vue'; //
import B_mine_planInfo from '../components/book/mine/planInfo.vue'; //
import B_mine_age from '../components/book/mine/age'; //
/*  */
import B_bookInfo from '../components/book/bookInfo/bookInfo.vue'; //
/*  */
import B_bookNew from '../components/book/list/new.vue'; //
import B_bookEnglish from '../components/book/list/english.vue'; //
import B_bookHot from '../components/book/list/hot.vue'; //
import B_bookSeries from '../components/book/list/series.vue'; //
import B_bookSeriesList from '../components/book/list/seriesList.vue'; //
/*  */
import B_bookCard from '../components/book/order/orderCard'; //
import B_bookCardInfo from '../components/book/order/orderCardInfo.vue'; //
import B_bookOrderInfo from '../components/book/order/orderInfo.vue'; //
import B_bookOrderMessage from '../components/book/order/orderMessage.vue'; //
/*  */
import B_bookSearch from '../components/book/search/search.vue'; //
import B_bookPerSearch from '../components/book/search/perSearch.vue'; //
import B_bookSearchRe from '../components/book/search/searchResult.vue'; //
import B_bookPerSearchRe from '../components/book/search/perSearchResult.vue'; //
/*  */
import B_success from "../components/book/common/success.vue"; //
import B_fail from "../components/book/common/fail.vue"; //
/*  */
import B_rules from "../components/book/common/rules"; //
// 
import  featured from "../components/book/Featured/index.vue";
//
import  books from "../components/book/Featured/books.vue";

const routes = [{
    path: '/index/:id',
    component: index,
    children: [{
        path: "",
        component: main
      },
      {
        path: "car",
        component: car
      },
      {
        path: "order",
        meta: {
          requireAuth: true,
        },
        component: order
      },
      {
        path: "mine",
        meta: {
          // 
          requireAuth: true,
        },
        component: mine
      },
    ]
  }, {
    path: '/',
    redirect: '/index/main',
    name: 'index',
    component: index
  },
  {
    path: '/chaneel',
    name: 'chaneel',
    component: chaneel
  },
  {
    path: '/login',
    name: 'login',
    component: login
  }, {
    path: '/logincode',
    name: 'logincode',
    component: logincode
  }, {
    path: '/register',
    name: 'register',
    component: register
  }, {
    path: '/forget',
    name: 'forget',
    component: forget
  }, {
    path: '/list',
    name: 'list',
    component: list
  }, {
    path: '/search',
    name: 'search',
    component: search
  }, {
    path: '/goodsinfo/:id',
    name: 'goodsinfo',
    component: goodsinfo
  }, {
    path: '/authInfo',
    name: 'authInfo',
    component: authInfo
  }, {
    path: '/authPage',
    name: 'authPage',
    component: authPage
  }, {
    path: '/goodslease/:id',
    name: 'goodslease',
    component: goodslease
  },
  {
    path: '/authentication',
    name: 'authentication',
    component: authentication
  }, {
    path: '/help',
    name: 'help',
    component: help
  },
  {
    path: '/card',
    name: 'card',
    component: card
  },
  {
    path: '/opinion',
    name: 'opinion',
    component: opinion
  }, {
    path: '/logistics',
    name: 'logistics',
    component: logistics
  },
  {
    path: '/agreement',
    name: 'agreement',
    component: agreement
  },
  {
    path: '/service',
    name: 'service',
    component: service
  }, {
    path: '/transaction',
    name: 'transaction',
    component: transaction
  }, {
    path: '/addressList',
    name: 'addressList',
    meta: {
      requireAuth: true,
    },
    component: addressList
  }, {
    path: '/editAddress/:id',
    name: 'editAddress',
    meta: {
      requireAuth: true,
    },
    component: editAddress
  }, {
    path: '/editAddress',
    meta: {
      requireAuth: true,
    },
    redirect: '/editAddress/add'
  }, {
    path: '/orderInfo/:id',
    meta: {
      requireAuth: true,
    },
    name: 'orderInfo',
    component: orderInfo
  }, {
    path: '/orderLogistics/:id',
    name: 'orderLogistics',
    component: orderLogistics
  }, {
    path: '/orderReturn',
    name: 'orderReturn',
    component: orderReturn
  },
  {
    path: '/goodsList/',
    name: 'goodsList',
    component: goodsList
  }, {
    path: '/collection',
    name: 'collection',
    component: collection
  }, {
    path: '/setting',
    name: 'setting',
    component: setting
  }, {
    path: '/about',
    name: 'about',
    component: about
  }, {
    path: '/shop/:id',
    name: 'shop',
    component: shop
  }, {
    path: '/shopInfo/:id',
    name: 'shopInfo',
    component: shopInfo
  },
  {
    path: '/selected',
    name: 'selected',
    component: selected
  },
  {
    path: '/download',
    name: 'download',
    component: download
  }, {
    path: '/orderAction/:id',
    name: 'orderAction',
    component: orderAction
  }, {
    path: '/orderSuccess',
    name: 'orderSuccess',
    component: orderSuccess
  }, {
    path: '/settlement',
    name: 'settlement',
    component: settlement
  }, {
    path: '/moreHot',
    name: 'moreHot',
    component: moreHot
  },{
    path: '/share',
    name: 'share',
    component: share
  },
  /*  */
  {
    path: '/book/:id',
    component: B_home,
    children: [{
        path: "",
        component: B_index
      },
      {
        path: "car",
        meta: {
          requireAuth: true,
        },
        component: B_car
      },
      {
        path: "collect",
        meta: {
          requireAuth: true,
        },
        component: B_collect
      },
      {
        path: "mine",
        meta: {
          // 
          requireAuth: true,
        },
        component: B_mine
      },
    ]
  }, {
    path: '/book',
    redirect: '/book/main',
    name: 'book',
    component: B_home
  },{
    path: '/mine_plan',
    name: 'B_mine_plan',
    component: B_mine_plan  
  },{
    path: '/mine_order',
    name: 'B_mine_order',
    component: B_mine_order  
  },{
    path: '/mine_setting',
    name: 'B_mine_setting',
    component: B_mine_setting  
  },{
    path: '/mine_age',
    name: 'B_mine_age',
    component: B_mine_age  
  },{
    path: '/book_bookInfo/:id',
    name: 'B_bookInfo',
    component: B_bookInfo  
  },{
    path: '/book_planInfo',
    name: 'B_mine_planInfo',
    component: B_mine_planInfo  
  }

  /*  */
  ,{
    path: '/book_bookNew',
    name: 'B_bookNew',
    component: B_bookNew  
  },{
    path: '/book_bookEnglish',
    name: 'B_bookEnglish',
    component: B_bookEnglish  
  },{
    path: '/book_bookHot',
    name: 'B_bookHot',
    component: B_bookHot  
  },{
    path: '/book_bookSeries',
    name: 'B_bookSeries',
    component: B_bookSeries  
  },{
    path: '/book_bookSeriesList',
    name: 'B_bookSeriesList',
    component: B_bookSeriesList  
  },
  /*  */
  {
    path: '/book_bookSearch',
    name: 'B_bookSearch',
    component: B_bookSearch  
  },{
    path: '/book_bookPerSearch',
    name: 'B_bookSearch',
    component: B_bookSearch  
  },{
    path: '/book_bookSearchRe',
    name: 'B_bookSearchRe',
    component: B_bookSearchRe  
  },{
    path: '/book_bookPerSearchRe',
    name: 'B_bookPerSearchRe',
    component: B_bookPerSearchRe  
  },
  /*  */
  {
    path: '/book_bookCard',
    name: 'B_bookCard',
    component: B_bookCard  
  },{
    path: '/book_bookCardInfo',
    name: 'B_bookCardInfo',
    component: B_bookCardInfo  
  },{
    path: '/book_bookOrderInfo',
    name: 'B_bookOrderInfo',
    component: B_bookOrderInfo  
  },{
    path: '/book_bookOrderMessage/:id',
    name: 'B_bookOrderMessage',
    component: B_bookOrderMessage  
  },{
    /*  */
    path: '/book_bookSuccess',
    name: 'B_success',
    component: B_success  
  },{
    /*  */
    path: '/book_bookFail',
    name: 'B_fail',
    component: B_fail  
  },{
    /*  */
    path: '/book_rule',
    name: 'B_rules',
    component: B_rules  
  },{
    /*  */
    path: '/featured',
    component: featured  
  },{
    // HTML
    path: '/books',
    component: books  
  }
  

  
];



export default new Router({
  mode: 'hash',
  routes
});



