
import CheckIcon from 'vux/src/components/check-icon/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import value2name from 'vux/src/filters/value2name.js'
import XHeader from 'vux/src/components/x-header/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XAddress from 'vux/src/components/x-address/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import ChinaAddressV3Data from 'vux/src/datas/china_address_v3.json'

import { mapGetters } from 'vuex'
import {API,getQuery} from '../../services'

export default {
components: {
XHeader,
Group,
Cell,
XButton,
XTextarea,
XInput,
XAddress,
CheckIcon,
Toast
},
data () {
return {
confrim:"",
toast:false,
success:false,
title:"",
addressData:ChinaAddressV3Data,
name:"",
phone:"",
address:[],
/*  */
addressDetils:"",
isDefault:false,
/* id */
id:"",
}
},
computed:{
...mapGetters([
'getUserInfoUserId',
'getUserInfoToken',
])
},
methods:{
routerBack(){
this.$router.goBack();
},
getName (value) {
return value2name(value, ChinaAddressV3Data)
},
/* parmas */
typeCheck(){
/*  */
if(this.$route.params.id=="add"){
this.title="";
this.name="";
this.phone="";
this.address=['','',''];
this.addressDetils="";
this.isDefault=false;
}else{
this.title="";
let id=this.$route.params.id;
this.id=id;
this.getAddress(id);
}
},
/*  */
getAddress(id){
API.person.addressInfo({
userId:this.getUserInfoUserId,
token:this.getUserInfoToken,
id:id,
is_default:0
}).then((res)=>{
if(res.body.code==200){
let addressList= res.body.data.addressList;
this.name=addressList.address_name;
this.phone=addressList.mobile;
let address=[];
address[0]=addressList.province;
address[1]=addressList.city;
address[2]=addressList.district;
this.address=address;
this.addressDetils=addressList.address;
this.isDefault=addressList.is_set_default==1?true:false;
}
})
},
/*  */
addAddress(){
/*  */
if(!this.name){
this.confrim="";
this.toast=true;
return false;
}
if(!this.phone){
this.confrim="";
this.toast=true;
return false;
}
if(!this.address[0]){
this.confrim="";
this.toast=true;
return false;
}
if(!this.addressDetils){
this.confrim="";
this.toast=true;
return false;
}
let addresslist=(this.getName (this.address)).split(" ");
if(this.title==""){
/*  */
API.person.addressEdit({
id:this.id,
userId:this.getUserInfoUserId,
token:this.getUserInfoToken,
address_name:this.name,
mobile:this.phone,
province:addresslist[0],
city:addresslist[1],
district:addresslist[2],
address:this.addressDetils,
postcode:null,
is_set_default:this.isDefault==true?1:0,
}).then((res)=>{
if(res.body.code==200){
this.confrim="";
this.success=true;
/*  */
setTimeout(() =>{
this.routerBack();
}, 1000);
}else{
this.confrim=res.body.msg;
this.toast=true;
}
});
}else{

/*  */
API.person.addressAdd({
userId:this.getUserInfoUserId,
token:this.getUserInfoToken,
address_name:this.name,
mobile:this.phone,
province:addresslist[0],
city:addresslist[1],
district:addresslist[2],
address:this.addressDetils,
postcode:null,
is_set_default:this.isDefault==true?1:0,
}).then((res)=>{
if(res.body.code==200){
this.confrim="";
this.success=true;
/*  */
setTimeout(() =>{
this.routerBack();
}, 1000);
}else{
this.confrim=res.body.msg;
this.toast=true;
}
});
}

}
},
mounted(){
this.typeCheck();
},
/*  */
activated(){
this.typeCheck();
}
}



