
import Scroller from 'vux/src/components/scroller/index.vue'
import XImg from 'vux/src/components/x-img/index.vue'
import Masker from 'vux/src/components/masker/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import { mapGetters } from 'vuex';
import { API, getQuery } from '../../services';

export default {
components: {
XImg,
LoadMore,
Actionsheet,
Scroller,
Masker,
Spinner,
XButton,
Group,
Cell,
Toast
},
data() {
return {
loading: false,
/*  */
temporary: [],
paydata: {},
confrim: "",
toast: false,
showList1: true,
scrollTop: 0,
onFetching: false,
bottomCount: 20,
currentType: 0,/* 0-67 */
/*  */
typeList: [{
name: "",
type: 0,
}, {
name: "",
type: 1,
}, {
name: "",
type: 2,
}, {
name: "",
type: 3,
}, {
name: "",
type: 4,
}, {
name: "",
type: 5,
}, {
name: "",
type: 6,
},],
/*  */
timeMap: { 1: "", 2: "", 3: "", 4: "", 5: "" },
/*  */
orderList: [[], [], [], [], [], [], []],
/* 7 */
currentPage: 1,
canBottom: true,
loadshow: false,
haveData: false,
/*  */
payMethod: 4,
menus: {
menu1: '',
menu2: ''
},
orderId: null,
payShow: false,
payTypeData: null,
}
},
computed: {
...mapGetters([
'getUserInfoUserId',
'getUserInfoToken',
]),
},
mounted() {
window.location.href="/?#/index/main/order";
overscroll(document.querySelector('.order_list'));
/* 0 */
localStorage.setItem("orderScroll", 0);
this.loading = true;
/*  */
this.orderList[this.currentType] = [];
/*  */
this.currentPage = 1;
this.haveData = true;
this.getTypeData(this.currentType);
let self = this;
/* self.getTypeData(this.currentType); */
function getScrollTop() {
var scrollTop = 0, bodyScrollTop = 0, documentScrollTop = 0;
if (document.querySelector('.order_list')) {
bodyScrollTop = document.querySelector('.order_list').scrollTop;
}
if (document.documentElement) {
documentScrollTop = document.documentElement.scrollTop;
}
scrollTop = (bodyScrollTop - documentScrollTop > 0) ? bodyScrollTop : documentScrollTop;
return scrollTop;
}

function getScrollHeight() {
var scrollHeight = 0, bodyScrollHeight = 0, documentScrollHeight = 0;
if (document.querySelector('.order_list')) {
bodyScrollHeight = document.querySelector('.order_list').scrollHeight;
}
if (document.documentElement) {
documentScrollHeight = document.documentElement.scrollHeight;
}
scrollHeight = (bodyScrollHeight - documentScrollHeight > 0) ? bodyScrollHeight : documentScrollHeight;
return scrollHeight;
}

function getWindowHeight() {
var windowHeight = 0;
if (document.compatMode == "CSS1Compat") {
windowHeight = document.documentElement.clientHeight;
} else {
windowHeight = document.body.clientHeight;
}
return windowHeight;
}
document.querySelector('.order_list').onscroll = function() {
if (getScrollTop() + getWindowHeight() >= (getScrollHeight() - 10)) {
if (self.canBottom == true) {
self.canBottom = false;
self.getTypeData(self.currentType);
}
}
};
},
activated() {
sessionStorage.setItem("mainReload",'ss');
/* url */
let biz_content = this.$route.query.biz_content;
/*  */
if (biz_content) {
biz_content = JSON.parse(biz_content);
let timetamp = JSON.parse(biz_content.invoke_state).timestamp;
if (((new Date().getTime() / 1000) - timetamp) <= 30) {
this.success(biz_content);
}
}
overscroll(document.querySelector('.order_list'));
/*  */
if (localStorage.getItem("reload")) {
this.loading = true;
/*  */
this.orderList[this.currentType] = [];
/*  */
this.currentPage = 1;
this.haveData = true;
this.getTypeData(this.currentType);
localStorage.setItem("reload", "");
}
/*  */
setTimeout(() => {
if (localStorage.getItem("orderScroll")) {
document.querySelector(".order_list").scrollTop = localStorage.getItem("orderScroll");
}
}, 100);
},
methods: {
/*  */
success(item) {
let self=this;
API.alipay.success({
out_order_no: item.out_order_no,
order_no: item.order_no,
invoke_state: item.invoke_state,
user_id: item.user_id,
admit_state: item.admit_state,
}).then((res) => {
if (res.body.code == 200) {
let openId = localStorage.getItem("openId");
API.order.OrderWechat({
userId: this.getUserInfoUserId,
token: this.getUserInfoToken,
orderSn: JSON.parse(item.invoke_state).order_sn,
payMethod: 4,
openId: item.user_id,
}).then((resopndy) => {
if (resopndy.body.code == 250) {
self.confrim = "";
self.toast = true;
setTimeout(() => {
self.orderList[self.currentType] = [];
self.currentPage = 1;
self.haveData = true;
self.getTypeData(self.currentType);
}, 1500);
} else if (resopndy.body.code == 300) {
self.confrim = resopndy.body.msg;
self.toast = true;
setTimeout(() => {
self.orderList[self.currentType] = [];
self.currentPage = 1;
self.haveData = true;
self.getTypeData(self.currentType);
}, 1500);
} else {
const div = document.createElement('div');
div.innerHTML = resopndy.body;
document.body.appendChild(div);
document.forms.alipaysubmit.submit();
}
})
}else{
alert(res.body.msg);
}
}, (err) => {
alert(JSON.stringify(err));
});
},
/*   0  1  2 */
isAlipay() {
var userAgent = navigator.userAgent.toLowerCase();
if (userAgent.match(/Alipay/i) == "alipay") {
this.payMethod = 4;
return 1;
} else if (userAgent.match(/MicroMessenger/i) == "micromessenger") {
this.payMethod = 3;
return 0;
} else {
return 2;
}
},
/* typelist */
typeselect(index) {
if (!this.canBottom) {
return false;
}
if (index == this.currentType) {
return false;
}
this.currentType = index;
this.orderList[this.currentType] = [];
this.currentPage = 1;
this.haveData = true;
this.getTypeData(this.currentType);
},
/*  */
getInfo(id) {
this.$router.push({
path: '/orderAction/' + id
});

localStorage.setItem("orderScroll", document.querySelector(".order_list").scrollTop);
},
/*  */
confrimType(item) {
if (item.order_is_delete == 0) {
if (item.order_refund_goods_status == 0) {
if (item.order_status == 1) {
if (item.order_shipping_status == 8) {
item.orderType = 8;//
} else {
item.orderType = 1;//
}
} else if (item.order_status == 2) {
switch (item.order_shipping_status) {
case 1:
item.orderType = 2;//
break;
case 2:
item.orderType = 3;//
break;
case 3:
item.orderType = 4;//
break;
case 4:
item.orderType = 5;//
break;
case 5:
item.orderType = 5;//
item.srcorderType = 2;
break;
case 6:
item.orderType = 6;//
break;
case 7:
item.orderType = 7;//
break;
}
}
} else if (item.order_refund_goods_status == 1) {
item.orderType = 9;//
} else if (item.order_refund_goods_status == 2) {
if (item.order_status == 2) {//
if (item.order_shipping_status == 2) {
item.srcorderType = 0;//
} else if (item.order_shipping_status == 3) {
item.srcorderType = 0;//
} else if (item.order_shipping_status == 5) {
item.srcorderType = 1;//
} else if (item.order_shipping_status == 6) {
item.orderType = 10;//
}
}
} else if (item.order_refund_goods_status == 4) {
if (item.order_shipping_status == 6) {
item.orderType = 10;//
}
} else if (item.order_refund_goods_status == 3) {
item.srcOrderType = 3;//
}
}
},
/*  */
/* ,div */
/*  */
getTypeData(status) {
if (this.haveData) {
this.loadshow = true;
API.order.orderlist({
userId: this.getUserInfoUserId,
token: this.getUserInfoToken,
orderStatus: status,
page_number: 10,
orderStatus: status,
page: this.currentPage,
}).then((res) => {
if (res.body.code == 200) {
let list = res.body.data.orderList.data
for (let item of list) {
this.confrimType(item);
}
this.orderList[this.currentType] = this.orderList[this.currentType].concat(list);
this.loadshow = false;
this.canBottom = true;
/*  */
this.loading = false;
overscroll(document.querySelector('.order_list'));
if (res.body.data.hasNext) {
this.currentPage++;
this.haveData = true;
} else {
this.haveData = false;
}
return false;
}
});
} else {
this.canBottom = true;
}
},
/*  */
deletOrder(id) {
let self = this;
this.$vux.confirm.show({
/* title: 'Title', */
content: '',
onConfirm() {
/*  */
API.order.orderDel({
userId: self.getUserInfoUserId,
token: self.getUserInfoToken,
orderId: id,
}).then((res) => {
if (res.body.code == 200) {
self.loading = true;
self.confrim = "";
self.toast = true;
self.orderList[self.currentType] = [];
self.currentPage = 1;
self.haveData = true;
self.getTypeData(self.currentType);
}
});
}
});

},
/*  */
onBridgeReady() {
let self = this;
WeixinJSBridge.invoke(
'getBrandWCPayRequest', self.paydata,
function(res) {
if (res.err_msg == "get_brand_wcpay_request:fail") {
self.confrim = "";
self.toast = true;
self.orderList[self.currentType] = [];
self.currentPage = 1;
self.haveData = true;
self.getTypeData(self.currentType);
}
if (res.err_msg == "get_brand_wcpay_request:cancel") {
self.orderList[self.currentType] = [];
self.currentPage = 1;
self.haveData = true;
self.getTypeData(self.currentType);
}
if (res.err_msg == "get_brand_wcpay_request:ok") {
self.confrim = "";
self.toast = true;
self.orderList[self.currentType] = [];
self.currentPage = 1;
self.haveData = true;
self.getTypeData(self.currentType);

}     // ,res.err_msg    ok
}
);
},
browserPay(key) {
if (key == 'menu1') {
/*  */
this.payMethod = 5;
} else if (key == 'menu2') {
this.payMethod = 4;
} else {
return false;
}
API.order.orderShipPay({
userId: this.getUserInfoUserId,
token: this.getUserInfoToken,
orderId: this.orderId,
}).then((res) => {
if (res.body.code == 200) {
let self = this;
let openId = localStorage.getItem("openId");
API.order.OrderWechat({
userId: this.getUserInfoUserId,
token: this.getUserInfoToken,
orderSn: res.body.data.order_big_sn,
payMethod: this.payMethod,
openId: openId,
}).then((resopndy) => {
if (resopndy.body.code == 250) {
self.confrim = "";
self.toast = true;
setTimeout(() => {
self.orderList[this.currentType] = [];
self.currentPage = 1;
self.haveData = true;
self.getTypeData(self.currentType);
}, 1500);
} else if (resopndy.body.code == 300) {
self.confrim = resopndy.body.msg;
self.toast = true;
setTimeout(() => {
self.orderList[this.currentType] = [];
self.currentPage = 1;
self.haveData = true;
self.getTypeData(self.currentType);
}, 1500);
} else {
self.payTypeData = resopndy.body;
if (key == 'menu1') {
/*  */
window.location.href = this.payTypeData;
} else if (key == 'menu2') {
const div = document.createElement('div');
div.innerHTML = this.payTypeData;
document.body.appendChild(div);
document.forms.alipaysubmit.submit();
}
}
}, (err) => {
self.confrim = "";
self.toast = true;
setTimeout(() => {
self.orderList[this.currentType] = [];
self.currentPage = 1;
self.haveData = true;
self.getTypeData(self.currentType);
}, 1500);
});
}
});

},
/*  */
payOrder(id, item) {
this.orderId = id;
/*  */
let self = this;
if (this.isAlipay() == 0) {
API.order.orderShipPay({
userId: this.getUserInfoUserId,
token: this.getUserInfoToken,
orderId: id,
}).then((res) => {
if (res.body.code == 200) {
let self = this;
let openId = localStorage.getItem("openId");
API.order.OrderWechat({
userId: this.getUserInfoUserId,
token: this.getUserInfoToken,
orderSn: res.body.data.order_big_sn,
payMethod: this.payMethod,
openId: openId,
}).then((resopndy) => {
if (resopndy.body.code == 250) {
self.confrim = "";
self.toast = true;
setTimeout(() => {
self.orderList[self.currentType] = [];
self.currentPage = 1;
self.haveData = true;
self.getTypeData(self.currentType);
}, 1500);
} else if (resopndy.body.code == 300) {
self.confrim = resopndy.body.msg;
self.toast = true;
setTimeout(() => {
self.orderList[self.currentType] = [];
self.currentPage = 1;
self.haveData = true;
self.getTypeData(self.currentType);
}, 1500);
} else {
/*  */
this.paydata = resopndy.body;
if (typeof WeixinJSBridge == "undefined") {
if (document.addEventListener) {
document.addEventListener('WeixinJSBridgeReady', self.onBridgeReady, false);
} else if (document.attachEvent) {
document.attachEvent('WeixinJSBridgeReady', self.onBridgeReady);
document.attachEvent('onWeixinJSBridgeReady', self.onBridgeReady);
}
} else {
self.onBridgeReady();
}
}
}, (err) => {
self.confrim = "";
self.toast = true;
setTimeout(() => {
self.orderList[this.currentType] = [];
self.currentPage = 1;
self.haveData = true;
self.getTypeData(self.currentType);
}, 1500);
});
}
});
/*  */
} else if (this.isAlipay() == 1) {
API.order.orderShipPay({
userId: this.getUserInfoUserId,
token: this.getUserInfoToken,
orderId: id,
}).then((res) => {
if (res.body.code == 200) {
let self = this;
let openId = localStorage.getItem("openId");
API.order.OrderWechat({
userId: this.getUserInfoUserId,
token: this.getUserInfoToken,
orderSn: res.body.data.order_big_sn,
payMethod: this.payMethod,
openId: openId,
}).then((resopndy) => {
if (resopndy.body.code == 250) {
self.confrim = "";
self.toast = true;
setTimeout(() => {
self.orderList[this.currentType] = [];
self.currentPage = 1;
self.haveData = true;
self.getTypeData(self.currentType);
}, 1500);
} else if (resopndy.body.code == 300) {
self.confrim = resopndy.body.msg;
self.toast = true;
setTimeout(() => {
self.orderList[this.currentType] = [];
self.currentPage = 1;
self.haveData = true;
self.getTypeData(self.currentType);
}, 1500);
} else {
const div = document.createElement('div');
div.innerHTML = resopndy.body;
document.body.appendChild(div);
document.forms.alipaysubmit.submit();
}
})
