},
/*  */
confrimOrder(id) {
let self = this;
this.$vux.confirm.show({
/* title: 'Title', */
content: '',
onConfirm() {
/*  */
API.order.orderReceipt({
userId: self.getUserInfoUserId,
token: self.getUserInfoToken,
orderId: id,
}).then((res) => {
if (res.body.code == 200) {
self.confrim = "";
self.orderList[this.currentType] = [];
self.currentPage = 1;
self.haveData = true;
self.getTypeData(self.currentType);
self.toast = true;
}
});
}
});
},
/*  */
confirmReturn(item) {
/* * sinceId 
* expressId 
* logisticsName 
* revertId 
* company  */
let self = this;
this.$router.push({ path: '/orderReturn?type=' + item.order_express_type + "&orderId=" + item.order_id });
localStorage.setItem("orderScroll", document.querySelector(".order_list").scrollTop);
},
/*  */
confrimSettlement(id) {
this.$router.push({
path: '/settlement', query: {
type: 1,
id: id,
}
});
localStorage.setItem("orderScroll", document.querySelector(".order_list").scrollTop);
},
/*  */
seeSettlement(id) {
this.$router.push({
path: '/settlement/?id=' + id
});

localStorage.setItem("orderScroll", document.querySelector(".order_list").scrollTop);
},
/*  , 1-2-*/
remind(id, type) {
let self = this;
API.order.orderRemind({
userId: self.getUserInfoUserId,
token: self.getUserInfoToken,
orderId: id,
type: type,
}).then((res) => {
if (res.body.code == 200) {
if (type == 1) {
self.confrim = "";
} else {
self.confrim = "";
}
self.toast = true;
}
});
},
/* ,, */
download(id) {
this.$router.push({
path: '/download'
});
localStorage.setItem("orderScroll", document.querySelector(".order_list").scrollTop);
}
}
}






