
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Popup from 'vux/src/components/popup/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import dateFormat from 'vux/src/tools/date/format.js'
import Toast from 'vux/src/components/toast/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'

import { mapGetters } from 'vuex'
import { API, getQuery } from '../../../services';
import HeaderCop from '../common/header.vue';
import BScroll from 'vue-betterscroll';
export default {
directives: {
TransferDom
},
components: {
Popup,
Actionsheet,
XHeader,
Group,
Cell,
XTextarea,
Toast,
CellBox,
BScroll,
HeaderCop
},
data() {
return {
payShow: false,
menus: {
menu1: '',
menu2: ''
},
/*  */
payMethod: 4,
canPay: true,
currentItem: {},//
cardShow: false,
confrim: "",
/*  */
toast: false,
orderLogistics: '/orderLogistics/',
/*  */
haveDefault: false,
/*  */
data: [],
cardList: [],//
/*  */
option: "",
title: "",
zmScore: 0,
maxNumber: 0,
haveUseList: [],//
service_area: ""//
}
},
computed: {
returnDesopte() {
let counter = 0;
for (const item of this.data) {
counter += (item.deposit - 0);
}
return counter;
},
...mapGetters([
'getUserInfoUserId',
'getUserInfoToken',
'getAddress',
'getNamePhone'
]),
/**@argument
* 
*/
allPrice() {
let counter = 0;
for (const item of this.data) {
let despote = 0;
if (this.zmScore < 650) {
despote = item.deposit - 0;
}
counter += (item.price - 0 + despote) * item.num;
}
counter -= this.allcoupon;
return counter < 0 ? 0 : counter;
},
allcoupon() {
let counter = 0;
for (const item of this.data) {
counter += (item.coupon - 0);
}
return counter;
},
deposit() {
let counter = 0;
for (const item of this.data) {
let despote = 0;
if (this.zmScore < 650) {
despote = item.deposit - 0;
} else {
return 0;
}
counter += despote * item.num;
}
return counter;
}
},
methods: {
/*  */
browserPay(key) {
if (key == 'menu1') {
/*  */
this.payMethod = 5;
} else if (key == 'menu2') {
this.payMethod = 4;
} else {
return false;
}
/*  */
let cardsList = [];
for (const item of this.data) {
cardsList.push({
card_id: item.card_id,
zujie_coupon: item.couponId ? item.couponId : "",
store_coupon: "",
num: item.num
})
}
let self=this;
API.book.cardOrder({
user_id: self.getUserInfoUserId,
token: self.getUserInfoToken,
address_id: self.$store.state.addressData.id || self.$store.state.addressData.address_id,
message: self.option,
cards: cardsList
}).then((res) => {
if (res.body.code == 200) {
let self = this;
let openId = localStorage.getItem("openId");
API.book.H5Pay({
userId: this.getUserInfoUserId,
token: this.getUserInfoToken,
orderSn: res.body.data.order_sn,
payMethod: this.payMethod,
openId: openId,
}).then((res) => {
if (res.body.code == 250) {
return false;
self.confrim = "";
self.toast = true;
setTimeout(() => {
self.$router.push({
path: '/book'
});
}, 1500);
} else if (res.body.code == 300) {
self.confrim = res.body.msg;
self.toast = true;
setTimeout(() => {
self.$router.push({
path: '/book'
});
}, 1500);
} else {
self.payTypeData = res.body;
if (key == 'menu1') {
window.location.href = this.payTypeData;
} else if (key == 'menu2') {
const div = document.createElement('div');
div.innerHTML = this.payTypeData;
document.body.appendChild(div);
document.forms.alipaysubmit.submit();
}
}

}, (err) => {
self.confrim = "";
self.toast = true;
self.$router.push({
path: '/book'
})
});
}
});

},
onBridgeReady() {
let self = this;
WeixinJSBridge.invoke(
'getBrandWCPayRequest', self.paydata,
function(res) {
localStorage.setItem("reload", "1");
if (res.err_msg == "get_brand_wcpay_request:fail") {
self.confrim = "";
self.toast = true;
setTimeout(() => {
self.$router.push({
path: '/book'
})
}, 500);
}
if (res.err_msg == "get_brand_wcpay_request:cancel") {
self.$router.push({
path: '/book'
})
}
if (res.err_msg == "get_brand_wcpay_request:ok") {
self.confrim = "";
self.toast = true;
setTimeout(() => {
self.$router.push({
path: '/book'
})
}, 500);
}     // ,res.err_msg    ok
}
);
},
/*   0  1  2 */
isAlipay() {
var userAgent = navigator.userAgent.toLowerCase();
if (userAgent.match(/Alipay/i) == "alipay") {
this.payMethod = 4;
return 1;
} else if (userAgent.match(/MicroMessenger/i) == "micromessenger") {
this.payMethod = 3;
return 0;
} else {
return 2;
}
},
cardshow(item) {
this.cardShow = true;
this.currentItem = item;
},
/*  */
closeCard() {
for (let i = 0; i < this.haveUseList.length; i++) {
if (this.haveUseList[i].coupon_no == this.currentItem.couponId) {
this.currentItem.couponId = "";
this.currentItem.coupon = 0;
this.cardList.push(this.haveUseList[i]);
this.haveUseList.splice(i, 1);
}
}
this.cardShow = false;
let i = [];
i = this.data;
this.data = [];
this.data = i;

},
goShop(item, index) {
this.closeCard();
this.currentItem.couponId = item.coupon_no;
this.currentItem.coupon = item.amount;
/*  */
this.haveUseList.push(item);
this.cardList.splice(index, 1);
this.cardShow = false;
let i = [];
i = this.data;
this.data = [];
this.data = i;
},
/**@argument

0  1 
*/
numberCheck(item, type) {
if (type == 0) {
if (item.num == 1
) {
return false;
} else {
item.num--;
this.maxNumber--;
let data = this.data;
this.data = [];
this.data = data;
}
} else {
if (this.maxNumber == 4) {
this.confrim = "4";
this.toast = true;
return false;
} else {
item.num++;
this.maxNumber++;
let data = this.data;
this.data = [];
this.data = data;
}
}

},
/**@argument
* 
*/
userZMScore() {
API.person.getUserZMScore({
user_id: this.getUserInfoUserId
}).then((res) => {

if (res.body.code == 200) {
if (res.body.msg == "") {
this.zmScore = res.body.data.zmscore;
}
}
})
},
/*  */
rentRange(type) {
let num = (type == 1 ? "1" : type == 2 ? "3" : type == 3 ? "6" : "12") - 0;
let self = this;
/*  */
let year = new Date().getFullYear(),
month = new Date().getMonth() + 1,
/*  */
day = new Date().getDate(),
/*  */
time = new Date().getTime();
let year_3 = Math.floor((month + num) / 12);
let month_3 = (month + num) % 12;
if (month_3 == 0) {
month_3 = 12;
year_3--;
}
let monthTime = new Date(year + year_3 + "/" + month_3 + "/" + day).getTime();
let start = dateFormat((new Date().getTime()), 'YYYY.MM.DD');
let end = dateFormat(monthTime - 24 * 3600 * 1000, 'YYYY.MM.DD');
return { start, end };
},
/*  */
scollRefresh() {
this.$refs.scollView.scroll.refresh();
},
routerBack() {
this.$router.goBack();
},
/*  */
buygoods() {
let self = this;
/*  */
if (!self.$store.state.addressData.mobile) {
this.confrim = "";
this.toast = true;
return false;
}
/*  */
let cardsList = [];
for (const item of this.data) {
cardsList.push({
card_id: item.card_id,
zujie_coupon: item.couponId ? item.couponId : "",
store_coupon: "",
num: item.num
})
}
if (this.isAlipay() == 0) {
API.book.cardOrder({
user_id: self.getUserInfoUserId,
token: self.getUserInfoToken,
address_id: self.$store.state.addressData.id || self.$store.state.addressData.address_id,
message: self.option,
cards: cardsList
}).then((res) => {
if (res.body.code == 200) {
let openId = localStorage.getItem("openId");
API.book.H5Pay({
userId: this.getUserInfoUserId,
token: this.getUserInfoToken,
orderSn: res.body.data.order_sn,
payMethod: this.payMethod,
openId: openId,
}).then((res) => {
/*  */
if (res.body.code == 250) {
self.confrim = "";
self.toast = true;
setTimeout(() => {
self.$router.push({
path: '/book'
});
}, 1500);
} else if (res.body.code == 300) {
self.confrim = res.body.msg;
self.toast = true;
setTimeout(() => {
self.$router.push({
path: '/book'
});
}, 1500);
} else {
self.paydata = res.body;
if (typeof WeixinJSBridge == "undefined") {
if (document.addEventListener) {
document.addEventListener('WeixinJSBridgeReady', self.onBridgeReady, false);
} else if (document.attachEvent) {
document.attachEvent('WeixinJSBridgeReady', self.onBridgeReady);
document.attachEvent('onWeixinJSBridgeReady', self.onBridgeReady);
}
} else {
self.onBridgeReady();
}
}
}, (err) => {
self.confrim = "";
self.toast = true;
self.$router.push({
path: '/book'
})
});
} else {
this.confrim = res.body.msg;
this.toast = true;
return false;
}
})
/*  */
} else if (this.isAlipay() == 1) {

} else {
this.payShow = true;
}
/*  */
},
getCardList() {
API.card.storeCard({
userId: this.getUserInfoUserId,
token: this.getUserInfoToken,
type: 1,
}).then((res) => {
if (res.body.code == 200) {
this.cardList = res.body.data;
}
});
},
/* vuex */
getDefaultAddress() {
API.book.defaultAddress({
user_id: this.getUserInfoUserId,
token: this.getUserInfoToken,
}).then((res) => {
if (res.body.code == 200) {
this.service_area = res.body.data.service_area;
if (res.body.data.default_address) {
this.haveDefault = true;
if (res.body.data.default_address) {
this.$store.dispatch('SetAddress', res.body.data.default_address);
}
} else {
this.haveDefault = false;
}
}
}).then(() => {
this.getCardList();
});
},

}, mounted() {
this.getDefaultAddress();
this.userZMScore();
},
activated() {
/*  */
if (localStorage.getItem('addressClick')) {
localStorage.setItem('addressClick', '');
API.book.checkAddress({
user_id: this.getUserInfoUserId,
token: this.getUserInfoToken,
address_id: this.$store.state.addressData.id || this.$store.state.addressData.address_id
}).then((res) => {
if (res.body.code != 200) {
this.confrim = res.body.msg;
this.canPay = false;
this.toast = true;
} else {
this.canPay = true;
}
});
this.haveDefault = true;
}
this.data = JSON.parse(localStorage.getItem("resultList"));
this.maxNumber = 0;
for (let item of this.data) {
this.maxNumber++;
item.num = 1;
item.coupon = 0;
item.start = this.rentRange(item.type).start;
item.end = this.rentRange(item.type).end;
}
}
}



