
import HeaderCop from '../common/header.vue';
import BScroll from 'vue-betterscroll';
import Scroller from 'vux/src/components/scroller/index.vue'
import XImg from 'vux/src/components/x-img/index.vue'
import Masker from 'vux/src/components/masker/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import { mapGetters } from 'vuex';
import { API, getQuery } from '../../../services';

export default {
components: {
XImg,
LoadMore,
Actionsheet,
Scroller,
Masker,
Spinner,
XButton,
Group,
Cell,
Toast,
BScroll,
HeaderCop
},
data() {
return {
title: "",
haveData: false,//
page: 1,
/*  */
swiper_pullUp: false,//
swiper_nodata: false,//
bookList: [],//
typeList: [
"", "", "", "", "", "", "", "", "", ""
],//
confrim: "",
toast: false,
scrollTop: 0,
onFetching: false,
page: 1,
orderId: null,
loading: false,
}
},
computed: {
...mapGetters([
'getUserInfoUserId',
'getUserInfoToken',
]),
},
mounted() {

},
activated() {
this.listRefresh();
},
methods: {
maxHeight(item) {
item.isHeight = !item.isHeight;
let data = this.bookList;
this.bookList = [];
this.bookList = data;
setTimeout(()=>{
this.scollRefresh();
},20);
},
/**
* 
*/
getInfo(id) {
this.$router.push({
path: '/book_bookOrderMessage/' + id
});
},
/**@argument
* 
*/
listRefresh() {
this.swiper_nodata = false;
this.page = 1;
this.bookList = [];
this.getList();
},
/**@argument
* 
*/
getList() {
if (this.loading) {
return false;
}
this.loading = true;
if (this.bookList.length >= 10) {
this.swiper_pullUp = true;
this.swiper_nodata = false;
}
API.book.orderList({
page: this.page,
page_number: 10,
user_id: this.getUserInfoUserId,
token: this.getUserInfoToken,
}).then((res) => {
if (res.body.code == 200) {
let time = 0;
if (this.bookList.length != 0) {
time = 500;
}
setTimeout(() => {
if (res.body.data.length > 0) {
this.swiper_pullUp = false;
this.bookList = this.bookList.concat(res.body.data);
this.page++;
} else {
this.swiper_pullUp = false;
if (this.bookList.length >= 10) {
this.swiper_nodata = true;
}
}
this.$nextTick(() => {
this.scollRefresh();
this.loading = false;
});

if (this.bookList.length == 0) {
this.haveData = true;
} else {
this.haveData = false;
}

}, time);
}
})
},
/*  */
scollRefresh() {
this.$refs.scollView.scroll.refresh();
},
/*  */
deletOrder(id) {
let self = this;
this.$vux.confirm.show({
/* title: 'Title', */
content: '',
onConfirm() {
/*  */
API.book.orderDel({
user_id: self.getUserInfoUserId,
token: self.getUserInfoToken,
order_id: id,
}).then((res) => {
if (res.body.code == 200) {
self.confrim = "";
self.toast = true;

self.listRefresh();
}
});
}
});

},
/** */
cancelOrder(id) {
let self = this;
this.$vux.confirm.show({
/* title: 'Title', */
content: '?',
onConfirm() {
/*  */
API.book.orderCancel({
user_id: self.getUserInfoUserId,
token: self.getUserInfoToken,
order_id: id,
}).then((res) => {
if (res.body.code == 200) {
self.confrim = "";
self.toast = true;

self.listRefresh();
}
});
}
});
},
/*  */
confrimOrder(id) {
let self = this;
this.$vux.confirm.show({
/* title: 'Title', */
content: '',
onConfirm() {
/*  */
API.book.orderreceipt({
user_id: self.getUserInfoUserId,
token: self.getUserInfoToken,
order_id: id,
}).then((res) => {
if (res.body.code == 200) {
self.confrim = "";
self.toast = true;
/**@argument
* 
*/
self.listRefresh();
}
});
}
});
},
/*  */
confirmReturn(item) {
/* * sinceId 
* expressId 
* logisticsName 
* revertId 
* company  */
let self = this;
this.$router.push({ path: '/orderReturn?orderId=' + item.order_id + '&from=book&book_id=' + item.books[0].book_id });
},
/*  , 1-2-*/
remind(id, type) {
let self = this;
API.book.subOrderMsg({
user_id: self.getUserInfoUserId,
token: self.getUserInfoToken,
order_id: id,
type: type,
}).then((res) => {
if (res.body.code == 200) {
if (type == 1) {
self.confrim = "";
} else {
self.confrim = "";
}
self.toast = true;
}
});
},
/* ,, */
download(id) {
this.$router.push({
path: '/download'
});
}
}
}






