
import XHeader from 'vux/src/components/x-header/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import dateFormat from 'vux/src/tools/date/format.js'
import Toast from 'vux/src/components/toast/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Popup from 'vux/src/components/popup/index.vue'

import { mapGetters } from 'vuex'
import { API, getQuery } from '../../services';

export default {
directives: {
TransferDom
},
components: {
Actionsheet,
XHeader,
Group,
Cell,
XTextarea,
Toast,
CellBox,
Popup
},
data() {
return {
payShow: false,
menus: {
menu1: '',
menu2: ''
},
/*  */
cardstate: {
id: "",//
storeId: "",//
},
/*  */
priceShow: false,
/*  */
storePrice: 0,
currentPrice: 0,
currentCard: 0,
cardShow: false,
confrim: "",
/*  */
toast: false,
toasts: false,
/*  */
tpl: {},
orderLogistics: '/orderLogistics/',
/*  */
haveDefault: false,
/* id */
cartId: 0,
/*  */
infoData: {
goodsFace: ""
},
/*  */
timeMap: { 1: "", 2: "", 3: "", 4: "", 5: "" },
/*  */
tplRules: {},
/*  */
option: "",
paydata: {
},
/*  */
/*  */
zjList: [],
/*  */
storeList: [],
/*  */
cardList: [],
/*  */
payMethod: 4,
payTypeData: null,
/*  */
currentCardType: 0,
/*  */
act_price: null,
relet_price:0,
/*  */
rent_period_min_rent:0,
storeInfo: null,
antDerate: 0
}
},
computed: {
...mapGetters([
'getUserInfoUserId',
'getUserInfoToken',
'getAddress',
'getNamePhone'
]),
/*  */
returnTpl() {
this.getTplPrice();
if (this.$store.state.tplId == 1) {
return "";
} else if (this.$store.state.tplId == 2) {
return "";
} else if (this.$store.state.tplId == 3) {
this.tpl = this.$store.state.sinceData;
return "";
} else {
return "";
}
},
/*  */
rentRange() {
let self = this;
let start = dateFormat(new Date(self.infoData.cart_start_time * 1000), 'YYYY-MM-DD');
let end = dateFormat(new Date(self.infoData.cart_end_time * 1000), 'YYYY-MM-DD');
return { start, end };
},
/*  */
/*  */
timeText() {
return this.timeMap[this.infoData.rent_period_type];
},
/*  */
goodsDespoit() {
if (this.infoData.goods_deposit == 0) {
return 0;
}
let despoite = this.infoData.goods_deposit * this.infoData.cart_content_good_amount - (this.goodsAllPrice);
if (despoite <= 0) {
return 0;
} else {
return despoite;
}
},
/*  */
goodsalipay() {
/* 000.1 */
let goodsDespoit = this.infoData.goods_deposit - (this.goodsDespoit - this.antDerate);
if (goodsDespoit <= 0) {
if (this.infoData.goods_deposit == 0) {
return 0.01;
} else {
return this.infoData.goods_deposit;
}
} else {
return goodsDespoit.toFixed(2);
}
},
/*  */
goodsTotolPrice() {
let allPrice = this.goodsAllPrice + this.currentPrice + this.storePrice;
allPrice = (allPrice <= 0 ? 0 : allPrice);
return allPrice + this.returnTplPrice + this.goodsDespoit;
},
/*  */
/* **++ */
goodsAllPrice() {
let Price;
if (this.act_price&&this.act_price!=0) {
if(this.relet_price&&this.relet_price!=0){
Price = this.act_price *this.infoData.cart_content_good_amount  *this.rent_period_min_rent+ this.infoData.cart_content_good_amount*(this.infoData.cart_time_number-this.rent_period_min_rent)*this.relet_price;
}else{
Price = this.act_price * this.infoData.cart_time_number * this.infoData.cart_content_good_amount;
}
} else {
if(this.relet_price&&this.relet_price!=0){
Price = this.infoData.rent_period_now_rent_price *this.infoData.cart_content_good_amount  *this.rent_period_min_rent+ this.infoData.cart_content_good_amount*(this.infoData.cart_time_number-this.rent_period_min_rent)*this.relet_price;
}else{
Price = this.infoData.rent_period_now_rent_price * this.infoData.cart_time_number * this.infoData.cart_content_good_amount;
}
}
console.log(Price);
return Price;
},
/*  */
returnTplPrice() {
let num = this.infoData.cart_content_good_amount;
let second = num - 1;
if (this.$store.state.tplId == 1) {
return this.tplRules.freight_first_price - 0 + ((this.tplRules.freight_continued_price - 0) * second);

} else if (this.$store.state.tplId == 2) {
return (this.tplRules.freight_door_cost - 0) * num;
} else if (this.$store.state.tplId == 3) {
this.tpl = this.$store.state.sinceData;
return 0;
} else {
return 0;
}
}
},
methods: {
/*  */
getArtPrice(rentType, dataObj) {
for (let item of dataObj) {
if (item.rent_period_type == rentType) {
return [item.act_price,item.relet_price,item.rent_period_min_rent];
}
}
return [0,0,0];
},
/*  */
browserPay(key) {
if (key == 'menu1') {
/*  */
this.payMethod = 5;
} else if (key == 'menu2') {
this.payMethod = 4;
} else {
return false;
}
API.order.orderPay({
userId: this.getUserInfoUserId,
token: this.getUserInfoToken,
info: [{
addressId: this.$store.state.addressData.id,//id 
cartId: this.cartId, //id
cart_tpl: this.$store.state.tplId,//
freight_continued_price: this.tplRules.freight_continued_price,//
freight_door_cost: this.tplRules.freight_door_cost,//
freight_first_price: this.tplRules.freight_first_price,//
goodsAmount: this.infoData.cart_content_good_amount,//
order_time_number: this.infoData.cart_time_number,//
rentTime: this.infoData.cart_start_time,//
rent_period_id: this.infoData.cart_content_rent_prieod_id,                    returnTime: this.infoData.cart_end_time,//
sinceId: this.$store.state.tplId == 3 ? this.$store.state.sinceData.since_id : "",//id
coupon_no: this.cardstate.id,//id,
store_coupon_no: this.cardstate.storeId,//id,
message: this.option,//
}]
}).then((res) => {
if (res.body.code == 200) {
let self = this;
let openId = localStorage.getItem("openId");
API.order.OrderWechat({
userId: this.getUserInfoUserId,
token: this.getUserInfoToken,
orderSn: res.body.data.order_big_sn,
payMethod: this.payMethod,
openId: openId,
}).then((resopndy) => {
if (resopndy.body.code == 250) {
self.confrim = "";
self.toast = true;
setTimeout(() => {
self.$router.push({
path: '/index/main/order'
});
}, 1500);
} else if (resopndy.body.code == 300) {
self.confrim = resopndy.body.msg;
self.toast = true;
setTimeout(() => {
self.$router.push({
path: '/index/main/order'
});
}, 1500);
} else {
self.payTypeData = resopndy.body;
if (key == 'menu1') {

window.location.href = this.payTypeData;
} else if (key == 'menu2') {
const div = document.createElement('div');
div.innerHTML = this.payTypeData;
document.body.appendChild(div);
document.forms.alipaysubmit.submit();
}
}

}, (err) => {
self.confrim = "";
self.toast = true;
self.$router.push({
path: '/index/main/order'
})
});
}
});

},
/*   0  1  2 */
isAlipay() {
var userAgent = navigator.userAgent.toLowerCase();
if (userAgent.match(/Alipay/i) == "alipay") {
this.payMethod = 4;
return 1;
} else if (userAgent.match(/MicroMessenger/i) == "micromessenger") {
this.payMethod = 3;
return 0;
} else {
return 2;

}
},
/*  */
goShop(item) {
/*  */
if (this.currentCardType == 0) {

this.cardstate.storeId = item.coupon_no;
this.storePrice = 0 - item.amount;
if (this.storePrice + this.goodsAllPrice <= 0) {
this.storePrice = 0 - this.goodsAllPrice;
}
/*  */
if (this.currentPrice != 0) {
if (this.storePrice + this.goodsAllPrice == 0) {
this.currentPrice = 0;
this.cardstate.id = "";
} else {
this.currentPrice = 0 - (this.goodsAllPrice + this.storePrice);
}
}
} else {
if (this.storePrice + this.goodsAllPrice == 0) {
this.confrim = "";
this.toast = true;
this.cardShow = false;
return false;
} else {
this.cardstate.id = item.coupon_no;
this.currentPrice = 0 - item.amount;
if (this.currentPrice + this.storePrice + this.goodsAllPrice <= 0) {
this.currentPrice = 0 - (this.goodsAllPrice + this.storePrice);
}
}
}
/*  */
this.priceShow = true;
this.cardShow = false;
},
/*  */
closeCard() {
/*  */
if (this.currentCardType == 0) {
/*  */
if (this.currentPrice != 0) {
if (this.currentPrice + this.goodsAllPrice <= 0) {
this.currentPrice = 0 - this.goodsAllPrice;
} else {
for (let item of this.zjList) {
if (item.coupon_no == this.cardstate.id) {
this.currentPrice = 0 - item.amount;
}
}
}
}
this.storePrice = 0;
this.cardstate.storeId = "";
} else {
this.cardstate.id = "";
this.currentPrice = 0;
}
this.cardShow = false;
},
/*   1 0*/
cardshow(type) {
this.currentCardType = type;
type == 1 ? this.cardList = this.zjList : this.cardList = this.storeList;
if (this.cardList.length == 0) {
this.confrim = "";
this.toast = true;
return false;
}
this.cardShow = true;
},
onBridgeReady() {
let self = this;
WeixinJSBridge.invoke(
'getBrandWCPayRequest', self.paydata,
function(res) {
localStorage.setItem("reload", "1");
if (res.err_msg == "get_brand_wcpay_request:fail") {
self.confrim = "";
self.toast = true;
setTimeout(() => {
window.location.href="/?#/index/main/order"
}, 500);
}
if (res.err_msg == "get_brand_wcpay_request:cancel") {
window.location.href="/?#/index/main/order"
}
if (res.err_msg == "get_brand_wcpay_request:ok") {
self.confrim = "";
self.toast = true;
setTimeout(() => {
window.location.href="/?#/index/main/order"
}, 500);
}     // ,res.err_msg    ok
}
);
},
routerBack() {
this.$router.goBack();
},
/*  */
buygoods() {
/*  */
if (this.getAddress == "") {
this.confrim = "";
this.toasts = true;
return false;
}
if (this.returnTpl == "") {
this.confrim = "";
this.toasts = true;
return false;
}
let self = this;
if (this.isAlipay() == 0) {
API.order.orderPay({
userId: this.getUserInfoUserId,
token: this.getUserInfoToken,
info: [{
addressId: this.$store.state.addressData.id,//id 
cartId: this.cartId, //id
cart_tpl: this.$store.state.tplId,//
freight_continued_price: this.tplRules.freight_continued_price,//
freight_door_cost: this.tplRules.freight_door_cost,//
freight_first_price: this.tplRules.freight_first_price,//
goodsAmount: this.infoData.cart_content_good_amount,//
order_time_number: this.infoData.cart_time_number,//
rentTime: this.infoData.cart_start_time,                        rent_period_id: this.infoData.cart_content_rent_prieod_id,                        returnTime: this.infoData.cart_end_time,                        sinceId: this.$store.state.tplId == 3 ? this.$store.state.sinceData.since_id : "",//id
coupon_no: this.cardstate.id,//id,
store_coupon_no: this.cardstate.storeId,//id,
message: this.option,
}]
}).then((res) => {
if (res.body.code == 200) {
let self = this;
let openId = localStorage.getItem("openId");
API.order.OrderWechat({
userId: this.getUserInfoUserId,
token: this.getUserInfoToken,
orderSn: res.body.data.order_big_sn,
payMethod: this.payMethod,
openId: openId,
}).then((resopndy) => {
/*  */
if (resopndy.body.code == 250) {
self.confrim = "";
self.toast = true;
setTimeout(() => {
self.$router.push({
path: '/index/main/order'
});
}, 1500);
} else if (resopndy.body.code == 300) {
self.confrim = resopndy.body.msg;
self.toast = true;
setTimeout(() => {
self.$router.push({
path: '/index/main/order'
});
}, 1500);
} else {
self.paydata = resopndy.body;
if (typeof WeixinJSBridge == "undefined") {
if (document.addEventListener) {
document.addEventListener('WeixinJSBridgeReady', self.onBridgeReady, false);
} else if (document.attachEvent) {
document.attachEvent('WeixinJSBridgeReady', self.onBridgeReady);
document.attachEvent('onWeixinJSBridgeReady', self.onBridgeReady);
}
} else {
self.onBridgeReady();
}
}
}, (err) => {
self.confrim = "";
self.toast = true;
self.$router.push({
path: '/index/main/order'
})
});
}
});
/*  */
} else if (this.isAlipay() == 1) {
API.order.orderPay({
userId: this.getUserInfoUserId,
token: this.getUserInfoToken,
info: [{
addressId: this.$store.state.addressData.id,//id 
cartId: this.cartId, //id
cart_tpl: this.$store.state.tplId,//
freight_continued_price: this.tplRules.freight_continued_price,//
freight_door_cost: this.tplRules.freight_door_cost,//
freight_first_price: this.tplRules.freight_first_price,//
goodsAmount: this.infoData.cart_content_good_amount,//
order_time_number: this.infoData.cart_time_number,//
rentTime: this.infoData.cart_start_time,                        rent_period_id: this.infoData.cart_content_rent_prieod_id,                        returnTime: this.infoData.cart_end_time,                        sinceId: this.$store.state.tplId == 3 ? this.$store.state.sinceData.since_id : "",//id
coupon_no: this.cardstate.id,//id,
store_coupon_no: this.cardstate.storeId,//id,
message: this.option,
}]
}).then((res) => {
if (res.body.code == 200) {
let self = this;
let openId = localStorage.getItem("openId");
/* order_id */
API.alipay.orderId({
userId: this.getUserInfoUserId,
Info: {
order_sn: res.body.data.order_big_sn,
goods_name: this.infoData.goodsName,
address: this.getAddress,
shop_name: this.infoData.store_name,
rent_amount: (this.goodsTotolPrice - this.antDerate),/*  */
deposit_amount: this.goodsalipay,//
borrow_time: this.infoData.cart_start_time,
expiry_time: this.infoData.cart_end_time,
order_id: res.body.data.order_id,
}
}).then((res) => {
if (res.body.code == 200) {
/* console.log(res.body.data.link_url); */
window.location.href = res.body.data.link_url;
}
}, (err) => {
});
/*  API.order.OrderWechat({
userId: this.getUserInfoUserId,
token: this.getUserInfoToken,
orderSn: res.body.data.order_big_sn,
payMethod: this.payMethod,
openId: openId,
}).then((resopndy) => {
const div = document.createElement('div');
div.innerHTML = resopndy.body;
document.body.appendChild(div);
document.forms.alipaysubmit.submit();
}) */
}
});
} else {
this.payShow = true;
}
},
/*  */
Initialization() {
this.storeInfo = this.$route.query.address;
this.antDerate = this.$route.query.antDerate;
this.cartId = this.$route.params.id;
API.order.orderConfirm({
userId: this.getUserInfoUserId,
token: this.getUserInfoToken,
cartId: [this.cartId],
}).then((res) => {
if (res.body.code != 200) {
this.$router.push({
path: '/index/main/order'
});
}
this.infoData = res.body.data.cart_list[0];
/*  */
this.act_price = this.getArtPrice(this.infoData.rent_period_type, this.infoData.rent_period)[0];
this.relet_price = this.getArtPrice(this.infoData.rent_period_type, this.infoData.rent_period)[1];
this.rent_period_min_rent=this.getArtPrice(this.infoData.rent_period_type, this.infoData.rent_period)[2];
this.orderLogistics = "/orderLogistics/" + this.infoData.cart_content_goods_id;
/*  1 2 */
API.card.storeCard({
userId: this.getUserInfoUserId,
token: this.getUserInfoToken,
storeId: this.infoData.store_id,
type: 1,
}).then((res) => {
if (res.body.code == 200) {
this.zjList = res.body.data;
}
});
/*  */
API.card.storeCard({
userId: this.getUserInfoUserId,
token: this.getUserInfoToken,
storeId: this.infoData.store_id,
type: 2
}).then((res) => {
if (res.body.code == 200) {
this.storeList = res.body.data;
}
});
}, (res) => {
this.$router.push({
path: '/index/main/order'
});
});
this.getDefaultAddress();
/*  */
this.$store.dispatch('ClearTpl');
},
/* vuex */
getDefaultAddress() {
API.person.addressInfo({
userId: this.getUserInfoUserId,
token: this.getUserInfoToken,
is_default: 1
}).then((res) => {
if (res.body.code == 200) {
this.haveDefault = true;
res = res.body.data.addressList;
this.$store.dispatch('SetAddress', res);
} else {
this.haveDefault = false;
}
});
},
/*  */
getTplPrice() {
if (this.$store.state.addressData.detail != "") {
API.order.tplPrice({
tpl: this.$store.state.tplId,
province: this.$store.state.addressData.province,
city: this.$store.state.addressData.city,
region: this.$store.state.addressData.district,
goodsId: this.infoData.cart_content_goods_id
}).then((res) => {
if (res.body.code == 200) {
this.tplRules = res.body.data;
}
});
}
}
}, mounted() {
overscroll(document.querySelector('.orderInfo_main_content'));
/*  */
this.Initialization();
},
activated() {
overscroll(document.querySelector('.orderInfo_main_content'));
/*  */
if (localStorage.getItem('orderClick')) {
localStorage.setItem('orderClick', '');
window.location.reload();
this.Initialization();
this.cardstate = {
id: "",
storeId: "",
};
this.priceShow = false;
/*  */
this.currentPrice = 0;
this.storePrice = 0;
}
/*  */
if (localStorage.getItem('addressClick')) {
localStorage.setItem('addressClick', '');
this.haveDefault = true;
}
}
}



