
import XHeader from 'vux/src/components/x-header/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Popup from 'vux/src/components/popup/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import { mapGetters } from 'vuex';
import SinceCompent from '../order/sinceCompent.vue';
import { API, getQuery } from '../../services';

export default {
directives: {
TransferDom
},
computed: {
...mapGetters([
'getUserInfoUserId',
'getUserInfoToken',
])
},
components: {
SinceCompent,
XHeader,
Group,
Cell,
CellBox,
Popup,
Toast
},
data() {
return {
orderId: "",
from: "",
/*  */
isSelect: "",
tplisSelect: "",
returnType: "",
confrim: "",
toast: false,
toasts: false,
/*  */
tplShow: false,
/*  */
sinceShow: false,
/*  */
tplList: [
{
name: "",
id: "shunfeng",
},
{
name: "",
id: "shentong",
},
{
name: "",
id: "zhongtong",
},
{
name: "",
id: "yuantong",
},
{
name: "",
id: "yunda",
},
{
name: "EMS",
id: "emsinten",
},
{
name: "",
id: "quanfeng",
},
{
name: "",
id: "tiantian",
},
{
name: "",
id: "debang",
},
{
name: "",
id: "huitong",
},
{
name: "",
id: "zhaijisong",
},
{
name: "",
id: "auto",
}, {
name: "",
id: "wu"
}
],
/*  */
sinceList: [],
infoData: {},
page: 1,
sincedata: {},
currentTpl: "",
code: "",
book_id: ""
}
},

methods: {
close() {
this.tplShow = false;
this.sinceShow = false;
},
/*  */
getData() {
API.order.orderInfo({
userId: this.getUserInfoUserId,
token: this.getUserInfoToken,
orderId: this.orderId,
}).then((res) => {
if (res.body.code == 200) {
this.infoData = res.body.data;
this.getReturnList();
}
});
},
/*  */
getReturnList() {
if (this.from == "book") {
API.book.revert_sel({
user_id: this.getUserInfoUserId,
token: this.getUserInfoToken,
book_id: this.book_id,
page: this.page,
page_number: 10,
}).then((res) => {
/*  */
if (res.body.code == 200) {
this.sinceList = this.sinceList.concat(res.body.data);
this.sincedata = this.sinceList[0];
this.isSelect = "",
this.sinceShow = false;
if (res.body.data.length == 10) {
this.page++;
this.getReturnList();
}
}
});
} else {
API.order.goodsRevert({
userId: this.getUserInfoUserId,
token: this.getUserInfoToken,
goodsId: this.infoData.goodsId,
page: this.page,
page_number: 10,
}).then((res) => {
/*  */
if (res.body.code == 200) {
this.sinceList = this.sinceList.concat(res.body.data.revert.data);
if (res.body.data.revert.data.length == 10) {
this.page++;
this.getReturnList();
}
}
});
}

},
/*  */
routerBack() {
this.$router.goBack();
},
/*  */
openPoup(type) {
if (type == 0) {
this.tplShow = true;
} else {
this.sinceShow = true;
}
},
/*  */
confrimSince(item) {
this.sincedata = item;
this.isSelect = "",
this.sinceShow = false;
},
/*  */
confrimTpl(item) {
this.currentTpl = item.id;
this.tplisSelect = item.name,
this.tplShow = false;
},
/*  */
orderBack() {
let self = this;
/*  */
if (!this.isSelect) {
this.confrim = "";
this.toasts = true;
return false;
}
this.$vux.confirm.show({
/* title: 'Title', */
content: '',
onConfirm() {
/*  */
if (self.from == "book") {
API.book.orderreturn({
user_id: self.getUserInfoUserId,
token: self.getUserInfoToken,
order_id: self.orderId,
express_id: self.code,
logistics_name: self.currentTpl,
revert_id: self.sincedata.revert_id,
company: self.tplisSelect,
}).then((res) => {
if (res.body.code == 200) {
self.confrim = "";
localStorage.setItem("reload", "1");
setTimeout(() => {
self.routerBack();
}, 500);
}else{
self.confrim = res.body.msg;
self.toast=true;
}
});
} else {
API.order.orderReturn({
userId: self.getUserInfoUserId,
token: self.getUserInfoToken,
orderId: self.orderId,
sinceId: "",
expressId: self.code,
logisticsName: self.currentTpl,
revertId: self.sincedata.revert_id,
company: self.tplisSelect,
}).then((res) => {
if (res.body.code == 200) {
self.confrim = "";
localStorage.setItem("reload", "1");
setTimeout(() => {
self.$router.push({ path: '/index/main/order' });
}, 500);
}
});

}


}
});


}
},
activated() {
overscroll(document.querySelector('.return_main_tplbox'));
overscroll(document.querySelector('.return_main_sinceList'));
this.page = 1;
this.isSelect = "";
this.tplisSelect = "";
this.infoData = "";
this.sincedata = "";
this.code = "";
this.orderId = this.$route.query.orderId;
this.from = this.$route.query.from;
this.book_id = this.$route.query.book_id;
this.sinceList = [];
this.getData();
},
mounted() {
overscroll(document.querySelector('.return_main_tplbox'));
overscroll(document.querySelector('.return_main_sinceList'));
}
}



