
import BookList from './common/bookList.vue';
import BScroll from 'vue-betterscroll';
import { mapGetters } from 'vuex';
import { API, getQuery } from '../../services';
import Toast from 'vux/src/components/toast/index.vue'
import Icon from 'vux/src/components/icon/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'
import Group from 'vux/src/components/group/index.vue'
import Calendar from 'vux/src/components/calendar/index.vue'

export default {
directives: {
TransferDom
},
components: {
XHeader,
Actionsheet,
ButtonTab,
ButtonTabItem,
BScroll,
Calendar,
Group,
Icon,
Toast
},
computed: {
...mapGetters([
'getUserInfoUserId',
'getUserInfoToken',
]),
},
data() {
return {
/*  */
swiper_pullUp: false,//
swiper_nodata: false,//
bookList: [],
title: "",
page: 1,
noData: false,
time: "",
weeksList: ['', '', '', '', '', '', ' '],
replaceTextList: { 'TODAY': '' },
startTime: "",
endTime: "",
delResult: [],//
selectResult: [],//
elseResult: [],//
selectAll: 0,//
max: 0,//
isEdit: 0,//
confrim: "",
toast: false,
loading: false,
noPlan: false,
}
},
watch: {
"selectResult":function(){
this.itemChange();
},
},
methods: {
/*  */
enterInfo() {
if(this.max==0){
this.confrim = "";
this.toast = true;
return false;
}
if (this.selectResult.length > this.max) {
this.confrim = "";
this.toast = true;
return false;
}
if (this.selectResult.length == 0) {
this.confrim = "";
this.toast = true;
return false;
}
if (this.time == "") {
this.confrim = "";
this.toast = true;
return false;
}
let localData = [];
for (let i of this.selectResult) {
for (let j of this.bookList) {
if (i == j.book_id) {
localData.push(j);
break;
}
}
}
/*  */
localStorage.setItem("bookOrder", JSON.stringify({
time: this.time,
data: localData
}));
this.$router.push({
path: "/book_bookOrderInfo"
})
},
checkType() {
if (this.isEdit == 0) {
this.elseResult = this.selectResult;
this.selectResult = this.delResult;
} else {
this.delResult = this.selectResult;
this.selectResult = this.elseResult;
}
this.isEdit = 1 - this.isEdit;
this.itemChange();
},
/**@argument
* 
*/
addColl() {
API.book.addColl({
user_id: this.getUserInfoUserId,
token: this.getUserInfoToken,
book_ids: this.selectResult
}).then((res) => {
if (res.body.code == 200) {
this.confrim = "";
this.toast = true;
}
})
},
/**@argument
* 
*/
delCar() {
let self = this;
this.$vux.confirm.show({
/* title: 'Title', */
content: '',
onConfirm() {
/*  */
API.book.cancelCar({
user_id: self.getUserInfoUserId,
token: self.getUserInfoToken,
book_ids: self.selectResult
}).then((res) => {
if (res.body.code == 200) {
self.listRefresh();
self.confrim = "";
self.toast = true;
}
})
}
});

},
/* checkbox */
itemChange() {
if (this.selectResult.length == this.bookList.length&&this.selectResult.length!=0) {
this.selectAll = 1;
} else {
this.selectAll = 0;
}
},
/*  */
allCheck() {
this.selectAll=1-this.selectAll;
if (this.selectAll == 0) {
this.selectResult = [];
} else {
this.selectResult = [];
for (const item of this.bookList) {
if ((item.has_stock == 1 && this.isEdit == 0) || this.isEdit == 1) {
this.selectResult.push(item.book_id);
}
}
}
},
/*  */
onChange(val) {
window.startTime = this.time;
this.listRefresh();
},
/**@argument
* 
*/
listRefresh() {
/*  */
this.bookList = [];
this.isEdit = 0;
this.swiper_nodata = false;
this.page = 1;
this.selectResult = [];
this.delResult = [];
this.selectAll = 0;
this.elseResult = [];
this.getList();
},
/**
* 
*/
getMax() {
API.book.maxCar({
user_id: this.getUserInfoUserId,
token: this.getUserInfoToken,
}).then((res) => {
if (res.body.code == 200) {
this.max = res.body.data.max_book_num;
let current = new Date().getTime();
this.startTime = this.dateFormat(current + res.body.data.min_days * 864e5, 'YYYY-MM-DD');
this.endTime = this.dateFormat(current + res.body.data.max_days * 864e5, 'YYYY-MM-DD');
this.noPlan = false;
} else {
this.noPlan = true;
}
});
},
/**@argument
* 
*/
getList() {
if (this.loading) {
return false;
}
this.loading = true;
if (this.bookList.length >= 10) {
this.swiper_pullUp = true;
this.swiper_nodata = false;
}
API.book.getCar({
page: this.page,
page_number: 10,
user_id: this.getUserInfoUserId,
token: this.getUserInfoToken,
start_date: this.time
}).then((res) => {
if (res.body.code == 200) {
let time = 0;
if (this.bookList.length != 0) {
time = 500;
}
setTimeout(() => {
if (res.body.data.length > 0 || this.page == 1) {
this.swiper_pullUp = false;
this.bookList = this.bookList.concat(res.body.data);
this.page++;
} else {
this.swiper_pullUp = false;
if (this.bookList.length >= 10) {
this.swiper_nodata = true;
}
}
this.$nextTick(() => {
this.scollRefresh();
this.loading = false;
if(this.page==2&&!window.startTime&&this.bookList.length!= 0){
setTimeout(()=>{
document.querySelector(".bookMain_carContent .vux-tap-active").click();
},20);
}
});
if (this.bookList.length == 0) {
this.noData = true;
} else {
this.noData = false;
}
}, time);
}
})
},
/*  */
scollRefresh() {
this.$refs.scollView.scroll.refresh();
},
},
mounted() {

},
activated() {
this.getMax();
this.listRefresh();
localStorage.setItem("choosePlan","");
}
}



