
import HeaderCop from '../common/header.vue';
import BScroll from 'vue-betterscroll';
import { API, getQuery } from '../../../services';
import { mapGetters } from 'vuex';
import XHeader from 'vux/src/components/x-header/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'
import Toast from 'vux/src/components/toast/index.vue'

export default {
directives: {
TransferDom
},
components: {
XHeader,
Actionsheet,
ButtonTab,
ButtonTabItem,
HeaderCop,
BScroll,
Toast
},
computed: {
...mapGetters([
'getUserInfoUserId',
'getUserInfoToken',
]),
},
data() {
return {
title: "",
haveData: false,
isChoose:0,//
bookList: [{
"total_use_times": 12,
/*  */
"type": 4,
/* 1-2-3-4- */
"card_id": 2,
/* card_id */
"month_use_times": 2,
/*  */
"total_remain": 12,
/*  */
"one_rent_num": 8,
/*  */
"card_no": "1234567890",
/*  */
"price": "599.00",
/* price */
"name": "",
/* name */
"invalid_time": 1888888888,
/*  */
"rank": 2,
/* 1-()2-()3- */
"month_remain": 1,
/*  */
"effect_time": 0,
/*  */
"status": 1
/* 1- 2- 3-*/
}],
confrim: "",
toast: false,
}
},
methods: {
routerBack() {
this.$router.goBack();
},
/**@argument
* 
*/
choosePlan(item){
if(this.isChoose==1){
let name=item.name;
localStorage.setItem('choosePlan', JSON.stringify({
name:name,
id:item.user_card_id
}));
this.routerBack();
}else{
localStorage.setItem("cardInfo",JSON.stringify({
child:[item]
}
));
this.$router.push({
path:'/book_planInfo'
})
}
},
getUserData(num){
API.book.myValidCards({
user_id: this.getUserInfoUserId,
token: this.getUserInfoToken,
book_num:num
}).then((res) => {
if(res.body.code==200){
this.bookList=res.body.data;
if(this.bookList.length==0){
this.haveData=true;
}else{
this.haveData=false;
}
}
});
},
/**@argument
* 
*/
getData() {
API.book.getPlan({
user_id: this.getUserInfoUserId,
token: this.getUserInfoToken,
}).then((res) => {
if(res.body.code==200){
this.bookList=res.body.data;
if(this.bookList.length==0){
this.haveData=true;
}else{
this.haveData=false;
}
}
})
},
/*  */
scollRefresh(){
this.$refs.scollView.scroll.refresh();
},
/*  */
success(item) {
let self=this;
API.alipay.success({
out_order_no: item.out_order_no,
order_no: item.order_no,
invoke_state: item.invoke_state,
user_id: item.user_id,
admit_state: item.admit_state,
}).then((res) => {
/* alert("") */
if (res.body.code == 200) {
/* alert(""); */
API.book.H5Pay({
userId: self.getUserInfoUserId,
token: self.getUserInfoToken,
orderSn: JSON.parse(item.invoke_state).order_sn,
payMethod: 4,
openId: 0,
}).then((resopndy) => {
/* alert(""); */
if (resopndy.body.code == 250) {
self.confrim = "";
self.toast = true;
} else if (resopndy.body.code == 300) {
self.confrim = resopndy.body.msg;
self.toast = true;
} else {
const div = document.createElement('div');
div.innerHTML = resopndy.body;
document.body.appendChild(div);
document.forms.alipaysubmit.submit();
}
},(err)=>{
/* alert(""); */
alert(JSON.stringify(err));
});
}else{
alert(res.body.msg);
}
}, (err) => {
alert("");
alert(JSON.stringify(err));
});
},
},

mounted() {

},
activated () {
this.isChoose=this.$route.query.choose||0;
if(this.isChoose==1){
this.title="";
this.getUserData(this.$route.query.num);
}else{
this.title="";
this.getData();
}
/* url */
let biz_content = this.$route.query.biz_content;
/*  */
if (biz_content) {
biz_content = JSON.parse(biz_content);
let timetamp = JSON.parse(biz_content.invoke_state).timestamp;
/* if (((new Date().getTime() / 1000) - timetamp) <= 30) { */
this.success(biz_content);

}
}
}



